:root {
  --weight-bg: #ffffff;
  --weight-color: #383838;
  --averages-bg: #233445;
  --averages-color: #d8d8d8;
  --graph-bg: #a060a0;
  --graph-color: #202080;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica", sans-serif;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
  color: inherit;
  font: inherit;
  cursor: pointer;
  outline: none;
  white-space: nowrap;
}

.page {
  height: 100vh;
}

.weight {
  height: 50%;
  background: #ffffff;
  background: var(--weight-bg);
  color: #383838;
  color: var(--weight-color);
}

.weights {
  padding: 8px 0;
}

.weightValues {
  display: grid;
  grid-template-columns: auto auto auto;
  -webkit-align-items: center;
          align-items: center;
  color: #202020;
}

.weightValues > * {
  padding-bottom: 8px;
}

.weightValues .weight {
  padding-left: 8px;
  margin-right: 8px;
  color: #0808c8;
  font-size: 1.6em;
}

.weightValues .time {
  color: #404040;
}

.weightValues button {
  border: 1px solid #808080;
  border-radius: 2px;
  padding: 0 2px;
  background: #d0d0d0;
  color: #606060;
  font-size: 0.8em;
}

.averages {
  height: 15%;
  padding: 8px;
  background: #233445;
  background: var(--averages-bg);
  color: #d8d8d8;
  color: var(--averages-color);
}

.average {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.graph {
  height: 35%;
  background: #a060a0;
  background: var(--graph-bg);
}

.Weight_inputBox__1WkK2 {
  height: 4em;
  padding: 4px;
  background: #d8d8d8;
}

.Weight_label__3jaH0 {
  font-weight: bold;
}

.Weight_input__3wmjV {
  display: grid;
  grid-template-columns: auto auto auto;
  -webkit-align-items: center;
          align-items: center;
}

.Weight_weights__2fWgZ {
  height: calc(100% - 4em);
  overflow-y: scroll;
}

